.main-header__toggle {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 44px;
  height: 36px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  right: -10px;
}
.main-header__toggle::before {
  position: absolute;
  top: 10px;
  right: 10px;
  content: "";
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-header__toggle::after {
  position: absolute;
  top: 26px;
  right: 10px;
  content: "";
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-header__toggle--active::before, .main-header__toggle:active::before {
  top: 18px;
  transform: rotate(45deg);
}
.main-header__toggle--active .main-header__toggle-center, .main-header__toggle:active .main-header__toggle-center {
  display: none;
}
.main-header__toggle--active::after, .main-header__toggle:active::after {
  top: 18px;
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__toggle:active + .main-navigation {
  display: block;
}

.main-header__toggle-center {
  position: absolute;
  top: 18px;
  right: 10px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
@media (min-width: 768px) {
  .main-header__toggle-center {
    display: none;
  }
}

.main-header__toggle--nojs .main-header__toggle-center {
  display: none;
}

.main-header__toggle--nojs.main-header__toggle::before,
.main-header__toggle--nojs.main-header__toggle::after {
  display: none;
}

.main-header__toggle--closed.main-header__toggle--nojs {
  cursor: unset;
}

.main-navigation {
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  top: 66px;
  background-color: #ffffff;
  z-index: 10;
}
.main-navigation--closed {
  display: none;
}
@media (min-width: 768px) {
  .main-navigation {
    max-width: 610px;
    position: static;
    display: flex;
    justify-content: flex-end;
    width: 100%;
    background-color: unset;
  }
}

.main-navigation--opened .main-navigation__list {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background-color: #ffffff;
}

.main-navigation--nojs .main-header__toggle {
  display: none;
}

@media (max-width: 768px) {
  .main-navigation--closed.main-navigation--nojs {
    display: block;
  }
}

.main-navigation--nojs .main-navigation__list {
  position: static;
  min-height: 0;
}

.main-navigation--nojs .main-navigation__item {
  display: block;
}

