.live-example__range {
  position: absolute;
  content: "";
  left: calc(50% - 2px);
  width: 4px;
  height: 100%;
  background-color: #666666;
  z-index: 2;
  cursor: pointer;
}
.live-example__range:hover {
  background-color: #68b738;
}
.live-example__range:hover .live-example__slider {
  border: 2px solid #68b738;
}
.live-example__range:hover .live-example__slider::before,
.live-example__range:hover .live-example__slider::after {
  background-color: #68b738;
}
.live-example__range:active {
  opacity: 0.6;
}

.live-example__slider {
  content: "";
  position: absolute;
  left: calc(50% - 20px);
  top: calc(50% - 22px);
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  border-radius: 50%;
  border: 2px solid #666666;
  z-index: 1;
  cursor: pointer;
}
.live-example__slider::before {
  content: "";
  position: absolute;
  clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
  background-color: #666666;
  width: 8px;
  height: 18px;
  top: 10px;
  right: 8px;
}
.live-example__slider::after {
  content: "";
  position: absolute;
  clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
  background-color: #666666;
  width: 8px;
  height: 18px;
  top: 10px;
  left: 8px;
  transform: rotate(180deg);
}

.before-after__range {
  margin-left: -20px;
  margin-right: -20px;
  position: relative;
}

